@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Castoro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    --body-color: #87bacd;
    --container-header: #468FB8;
    --container-body: #c7dde9;
}

.row {
    min-height: 100vh;

    background-color: var(--body-color);
}

.editor {
    width: 600px;
}

@media only screen and (max-width: 850px) {
    .editor {
        width: 550px;
    }
}

@media only screen and (max-width: 650px) {
    .editor {
        width: 80%;
    }
}

@media only screen and (max-width: 500px) {
    .editor {
        width: 85%;
    }
}

@media only screen and (max-width: 400px) {
    .editor {
        width: 90%;
    }
}

.editor .editor-body {
    height: 230px;
}

.max-editor {
    width: 95%;
    height: 96vh;
}

.previewer {
    width: 800px;
}

@media only screen and (max-width: 850px) {
    .previewer {
        width: 650px;
    }
}

@media only screen and (max-width: 650px) {
    .previewer {
        width: 98%;
    }
}

.previewer .previewer-body {
    min-height: 180px;
}

.max-previewer {
    width: 95%;
    min-height: 96vh;

    background-color: var(--container-body);
}

.header {
    background-color: var(--container-header);
}

.header span {
    font-size: 16px;
    font-family: 'Russo One', sans-serif;
}

.header path:hover {
    cursor: pointer;

    color: #a2c7db;
}

.editor-body {
    font-family: 'Cutive Mono', monospace;
    line-height: normal;
}

.previewer-body {
    font-family: 'Castoro', serif;
    
}    

.editor-body,
.previewer-body {
    font-size: 14px;

    background-color: var(--container-body);
}

.previewer-body h1 {
    font-size: 28px;
    font-weight: bold;
    border-bottom: 2px solid black;
    margin: 1.25rem 0;
}

.previewer-body h2 {
    font-size: 22px;
    font-weight: bold;
    border-bottom: 1px solid black;
    margin: 1.25rem 0;
}

.previewer-body h3 {
    font-size: 16px;
    font-weight: bold;
    margin: 1.25rem 0;
}

.previewer-body code {
    padding: 1px 4px 2px 4px;

    background-color: white;
    color: black;
}

.previewer-body pre {
    padding: 5px 0 5px 5px;
    background-color: white;
}

.previewer-body blockquote {
    padding-left: 5px;
    margin-left: 25px;
    border-left: 3px solid #152a37;

    color: #152a37;
}

.previewer-body table,
.previewer-body th,
.previewer-body td {
    border: 2px solid black;
    border-collapse: collapse;
    padding: 0 5px;
}

.previewer-body th {
    text-align: center;
}
